// Importamos la librera fonk de Lemoncode y creamos nuestro esquema de validaciones. En este caso, vamos a utilizar la validacin del email y de los campos requeridos. Aprovechamos tambin para crear un mensaje en espaol para que sea el que aparezca en la pgina cuando no supere las validaciones. 

import { Validators, createFormValidation } from '@lemoncode/fonk';

const validationSchema = {
    field: {
        user: [
            {
            validator: Validators.required,
            message: 'Campo requerido',
            },
            {
            validator: Validators.email,
            message: 'Email no vlido',
            },
        ],
        password: [
            {
            validator: Validators.required,
            message: 'Campo requerido',
            },
        ],
    },
};

export const formValidation = createFormValidation(validationSchema);